.wrapper {
  height: 100%;
  position: relative;
  padding-bottom: 47px;
}
.wrapper_container {
  height: 100%;
  overflow-y: auto;
}
.wrapper_container_info {
  position: relative;
  padding: 50px 15px 0;
}
.wrapper_container_info_avator {
  position: absolute;
  top: 0;
  left: 50%;
  width: 80px;
  height: 80px;
  margin: -40px 0 0 -40px;
  padding: 1px;
  border-radius: 50%;
  background-color: #FFF;
}
.wrapper_container_info_avator img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.wrapper_container_info_detail {
  text-align: center;
  padding-bottom: 8px;
  border-bottom: 1px dashed rgba(0, 0, 0, 0.1);
}
.wrapper_footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
}
